.dark {
    background-color: #121528;
}

.body-container {

    .right-bg {
        position: absolute;
        top: 0;
        right: 0;
        // background-color: #5D54A4;
        height: 800px;
        width: 40%;
    }

    .right-line {
        position: absolute;
        width: 100px;
        height: 900px;
        right: 100px;
        top: 0px;
        // background-color: #ffffff;
        opacity: 0.2;
    }
}

@media (max-width: 767px) {
    .body-container {

        .right-bg {
            display: none;
        }

        .right-line {
            display: none;
        }
    }
}