.form-info {
  display: flex;
  justify-content: center;
  align-items: center;

  .text {
    color: white;
    text-align: center;
    margin: 30px 0;
  }

  .contact-icons {
    display: flex;
    justify-content: center;

    svg {
      width: 30px;
      height: 30px;
      fill: white;
      opacity: 0.6;
      margin: 0;

      &:hover {
        fill: rgb(42, 61, 102);
      }
    }

    a {
      margin: 0 15px;
    }
  }
}

.form {
  h4 {
    display: flex;
    margin: 30px 0;
    justify-content: left;
  }

  input {
    background-color: transparent;

    &:focus {
      background-color: transparent;
      border: 1px solid purple;
      box-shadow: none;
    }
  }

  textarea {
    background-color: transparent;

    &:focus {
      background-color: transparent;
      border: 1px solid purple;
      box-shadow: none;
    }
  }

  &-row {
    margin: 20px 0;
  }

  .buttons {
    display: flex;
    margin: 20px 0;
    justify-content: flex-end;

    .send {
      background-color: #637dfb;
      border-color: #637dfb;

      &:hover {
        color: #cbc4ff;
        background-color: #5d54a4;
      }

      &:active {
        background-color: #6b088a !important;
      }
    }

    .cancel {
      border: none;
      color: red;
      margin-right: 35px;
    }
  }
}
