$card-height-large-screen: 20rem;

.projects-section {
    padding-bottom: 70px;

    h2 {
        display: flex;
        padding: 30px;
        justify-content: center;
        color: #4f0473;
    }

    .cards {
        justify-content: center;

        .card {
            margin: 15px;
            display: inline-flex;
            position: relative;
            height: 300px;
            width: 300px;
            color: white;
            overflow: hidden;
            border-radius: 10px;
            box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);

            img {
                width: 100%;
                height: 100%;
                display: block;
                transition: transform 3s ease;
            }

            &:hover {
                img {
                    transform: scale(1.2);
                    transition: transform 3s ease;
                }

                .card-title {
                    display: none;

                }

                .card-text {
                    max-height: $card-height-large-screen;
                    opacity: 1;

                    h5 {
                        color: #dda2ff;
                    }
                }
            }

            &-title {
                position: absolute;
                bottom: 0rem;
                padding: 1rem;
                color: white;
                transition: color 1s ease;
                margin-bottom: .5rem;
                z-index: 1;

            }

            &-text {
                position: absolute;
                bottom: 0rem;
                padding: 1rem;
                color: white;
                max-height: 0;
                opacity: 0;
                transition: max-height 1s ease, opacity 1s ease;
                background-color: rgba(0, 0, 0, 0.6);
            }

            &-img-overlay {
                background: rgba(0, 0, 0, 0) linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.7) 80%);
                padding: 0;
            }
        }
    }
}