.knowledge-section {
    padding-bottom: 70px;

    h2 {
        display: flex;
        padding: 30px;
        justify-content: center;
    }

    svg {
        height: 70px;
        width: 70px;
        margin-top: 15px;
        fill: #5d54a4;
    }


    .card {
        margin: 5px;
        border: none;
        transition: all .4s ease 0s;
        -webkit-transition: all .4s ease 0s;
        -moz-transition: all .4s ease 0s;
        -o-transition: all .4s ease 0s;

        .card-body {
            justify-content: center;

            .card-title {
                display: flex;
                justify-content: center;
                color: rgb(99, 125, 251);
            }

            .card-text {
                text-align: center;
            }

            .more-title {
                margin: 5px;
                text-align: center;
                font-weight: bold;
                color: rgb(99, 125, 251);
            }

            .more-text {
                text-align: center;
            }
        }

        &:hover {
            background-color: #687AFB !important;
            color: white;

            .card-title {
                color: white;
            }

            .card-text {
                text-align: justify;
                text-align: center;
            }

            .more-title {
                color: white;
                text-align: center;
            }

            .more-text {
                text-align: center;
            }

            svg {
                fill: white;
            }
        }
    }
}