.about-section {
    display: flex;
    padding: 200px 0 70px;
    justify-content: center;
    align-items: center;

    hr {
        opacity: 0.4;
        margin-left: 0;
        border-top: 2px solid !important;
        width: 255px;
    }

    .about-avatar {

        svg {
            z-index: 1;
            width: 383.406px;
            height: 575.078px;
            margin: auto;
        }

        .avatar {

            .rectangle1 {
                z-index: -1;
                position: absolute;
                width: calc(441px*0.55);
                height: calc(627px*0.55);
                left: 35px;
                top: 99px;
            }

            .rectangle2 {
                z-index: -1;
                position: absolute;
                width: calc(441px*0.55);
                height: calc(627px*0.55);
                left: 120px;
                top: 150px;
            }
        }

        .circle1 {
            position: absolute;
            height: 50px;
            width: 50px;
            fill: rgba(0, 240, 255, 0.3);
            left: 300px;
            top: 90px;
        }

        .circle3 {
            position: absolute;
            height: 50px;
            width: 50px;
            fill: rgba(92, 177, 238, 0.5);
            left: 40px;
            top: 450px;
        }

    }

    .about-me {
        top: 50px;

        .description {
            position: relative;

            p {
                text-align: justify;
            }

            .circle2 {
                position: absolute;
                height: 40px;
                width: 40px;
                fill: rgba(93, 84, 164, 0.2);
                left: 280px;
                top: 30px;
            }

            .square {
                position: relative;
                height: 30px;
                width: 30px;
                fill: rgba(176, 61, 231, 0.4);
                opacity: 0.7;
                top: 0px;
            }
        }
    }

}

@media (max-width: 400px) {
    .about-section {
        padding: 40px 0 70px;

        .about-avatar {
            svg {
                display: block;
                width: 260.406px;
                height: 443.078px;

            }

            .circle1 {
                left: 14px;
            }

            .circle3 {
                top: 375px;
                opacity: .5;
            }

            .avatar {

                .rectangle1 {
                    width: calc(441px*0.30);
                    height: calc(627px*0.45);
                    left: 55px;
                }

                .rectangle2 {
                    width: calc(441px*0.30);
                    height: calc(627px*0.45);
                    left: 130px;
                }

            }
        }

        .about-me {
            .description {
                .circle2 {

                    left: 150px;
                    top: 2px;
                    opacity: .4;
                }

                hr {
                    width: 100%;
                }
            }
        }

    }
}