.contact-banner {

    position: relative;
    display: inline-flex;
    align-items: center;
    background-color: #5D54A4;
    top: 150px;
    height: 300px;
    width: 100%;
    margin-bottom: 75px;

    svg {
        width: 383.406px;
        height: 575.078px;
    }

    .contact-me {
        display: flex;
        position: relative;
        justify-content: center;
        align-items: center;

        .contact-info {

            position: relative;
            color: white;
            justify-content: center;

            svg {
                width: 50px;
                height: 50px;
                fill: white;
                margin: 0;

                &:hover {
                    fill: rgb(42, 61, 102);
                }
            }

            .contact-icons {
                display: flex;
                justify-content: center;

                a {
                    margin: 15px;
                }
            }
        }
    }
}

@media (max-width: 767px) {

    .contact-banner {

        .contact-avatar {
            display: none;
        }
    }
}