.left-col {
  display: flex;
  align-items: center;
  justify-content: center;

  .left-panel {
    display: block;

    .left-text {
      display: block;
      align-items: center;
      margin: 140px 0 0 0;

      h1 {
        font-size: 3.5rem;
      }

      h2 {
        font-size: 3rem;
      }
    }

    .contact-icons {
      display: flex;

      svg {
        width: 30px;
        height: 30px;
        fill: rgb(99, 125, 251);
        opacity: .6;
        margin: 0;

        &:hover {
          fill: rgb(42, 61, 102);
        }
      }

      a {
        margin: 0 15px;

      }
    }

    button {
      color: #637DFB;
      border-color: #637DFB;
      display: block;
      margin: 20px 0;

      &:hover {
        color: #cbc4ff;
        background-color: #5d54a4;
      }

      &:active {
        background-color: #6b088a !important;
      }
    }

    .circles {
      display: flex;
      align-items: flex-end;
      margin-top: 15px;

      .circle1 {
        margin: 0 15px;

        height: 140px;
        width: 140px;
      }

      .circle2 {
        margin: 0 15px;

        height: 100px;
        width: 100px;
      }

      .circle3 {
        margin: 0 15px;

        height: 75px;
        width: 75px;
      }
    }
  }
}

.hi-avatar {
  opacity: 0.95;
  justify-content: center;
  width: 365px;
  height: 575.078px;

  svg {
    width: 383.406px;
    height: 575.078px;
  }
}

.setup {
  display: none;
}

@media (max-width: 767px) {
  .setup {
    display: flex;

    svg {
      height: 240px;
      opacity: .7;
    }
  }

  .left-col {
    .left-panel {
      .left-text {

        h1 {
          font-size: 2.5rem;
        }

        h2 {
          font-size: 2rem;
        }
      }

      .circles {
        display: none;
      }
    }
  }

  .hi-avatar {
    display: none;
  }
}